*:root {
    --primary-color: #001427;
    --secondary-color: #8D0801;
    --tertiary-color: #708D81;
    --quaternary-color: #F4D58D;
    --extra-color: #BF0603;
    --border-radius: 1.5rem;
    --spacing-one: calc(1rem * 0.25);
    --spacing-two: calc(1rem * 0.5);
    --spacing-three: 1rem;
    --spacing-four: calc(1rem * 1.5);
    --spacing-five: calc(1rem * 3);
}

  * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }
    
    body {
      font-family: Helvetica, Arial, sans-serif;
      color: white;
}
.page {
  min-height: 100vh;
}
.bg {
    background-color: var(--primary-color);
}

.bg-image {
    background-image: url('../images/mahogany-hero.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}
input {
  width: 100%;
}
textarea {
  width: 100%;
}
.project-thumbnail {
    width: 300px;
    height: 100px;
}

.profile-thumbnail {
    width: 200px;
    height: 200px;
}

.nav-link {
    text-decoration: none;
    font-weight: bold;
}
.nav-link-active {
    text-decoration: none;
    font-weight: bold;
    color: var(--quaternary-color);
}

li {
    list-style: none;
}

.border {
  border: var(--primary-color);
}

img {
  pointer-events: none;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: var(--quaternary-color);
}

nav a:hover{
  background-color: var(--tertiary-color);
}

#link-icons {
  font-size: 25px;
}


